export const getEventCommentary = (snailName: string, eventName: string): string => {
  const templates: { [key: string]: string[] } = {
    ' ': [
      `${snailName}     !`,
      `! ${snailName}    !`,
      `${snailName} ,   !`
    ],
    ' ': [
      `${snailName}   ...`,
      `! ${snailName}   !`,
      `${snailName} ,   .`
    ],
    ' ': [
      `${snailName}    !`,
      `! ${snailName}   !`,
      `${snailName} ,   !`
    ],
    ' ': [
      `${snailName}    !`,
      `${snailName} ,  ~`,
      `! ${snailName}   .`
    ],
    ' ': [
      `! ${snailName}   !`,
      ` ! ${snailName}   !`,
      `${snailName} ,   ...`
    ],
    ' ': [
      `! ${snailName}    !`,
      `${snailName} ,   ON!`,
      `   ! ${snailName}  !`
    ],
    '  ': [
      `? ${snailName}   !`,
      `${snailName} ,   ?`,
      ` ${snailName} ,   !`
    ],
    ' ': [
      `! ${snailName}   !`,
      `${snailName}   ?`,
      ` ! ${snailName}  !`
    ],
    ' ': [
      `${snailName}    !`,
      ` ${snailName}   !`,
      `${snailName} ,   ~`
    ]
  };

  const messages = templates[eventName] || [`${snailName}   !`];
  return messages[Math.floor(Math.random() * messages.length)];
};

export const getRaceCommentary = (snails: any[], leaders: string[]): string => {
  const messages = [
    `${leaders[0]}    !`,
    `  ! ${leaders.slice(0, 2).join(', ')} !`,
    `    !`,
    `${leaders[0]}   !`,
    `! ${leaders.slice(0, 3).join(', ')}   !`
  ];
  
  return messages[Math.floor(Math.random() * messages.length)];
};

export const getFinishCommentary = (winnerName: string, rank: number): string => {
  if (rank === 1) {
    return ` ${winnerName}  1  !`;
  } else if (rank === 2) {
    return `${winnerName}  2 !`;
  } else if (rank === 3) {
    return `${winnerName} , 3 !`;
  }
  return `${winnerName}  ${rank} .`;
};